import React, { Component } from 'react';
import clsx from 'clsx';
import CssBaseline from '@material-ui/core/CssBaseline';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Box from '@material-ui/core/Box';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Switch from '@material-ui/core/Switch';
import NativeSelect from '@material-ui/core/NativeSelect';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Link from '@material-ui/core/Link';
import { styled } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { VictoryPie, VictoryChart, VictoryBar, VictoryTheme, VictoryAxis, VictoryContainer } from 'victory';
import data from './data';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import { grey } from '@material-ui/core/colors';
import _ from 'lodash';


function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://material-ui.com/">
        Police Costs
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
};

const capitalize = (str, lower = false) =>
  (lower ? str.toLowerCase() : str).replace(/(?:^|\s|["'([{])+\S/g, match => match.toUpperCase());
;

const theme = createMuiTheme({
  palette: {
    type: 'dark',
    primary: {
      // Purple and green play nicely together.
      main: grey[900],
    },
    secondary: {
      // This is green.A700 as hex.
      main: grey[100],
    },
  },
  typography: {
    h1: {
      textAlign:'left'
    },
    h2: {
      textAlign: 'left'
    },
    p: {
      textAlign: "left"
    }
  }
});

const Title = styled(Typography)({
  textAlign: 'left',
  padding: '0 0 20px 0',
  fontWeight: 'bold'
});

const StateButton = styled(Button)({
  margin: '10px',
  padding: '1rem',
  textTransform: 'none',
  fontSize: '2rem'
});

const Crumb = styled(Link)({
  textAlign:'left',
  float:'left',
  display:'inline',
  padding:'10px 3px'
});


class Dashboard extends Component {

  constructor(props) {
    super(props);
    const { classes } = props;
    this.state = {
      data: data.states,
      cities: [],
      activeStateName:"",
      activeCity: [],
      activeCityName: ""
    }; 
  }
  
  componentDidMount() {
  }
  
  handleState(state) {
    let stateIndex = _.findIndex(this.state.data, function(o) { return o.state == state; });
    this.setState({
      storeData: this.state.data,
      cities: this.state.data[stateIndex].cities,
      activeStateName: this.state.data[stateIndex].state,
      data:[]
    })
  }
  
  returnHome(){
    this.setState({
      data:this.state.storeData,
      storeData:[],
      storeCities:[],
      storeStateName:[],
      cities: [],
      activeCity:[],
      activeStateName:"",
      activeCityName:"",
    })
  }

  handleCity(city) {
    let cityIndex = _.findIndex(this.state.cities, function(o) { return o.city == city; });
    this.setState({
      storeCities: this.state.cities,
      activeCity: [this.state.cities[cityIndex]],
      activeCityName:this.state.cities[cityIndex].city,
      cities:[]
    })
  }

  returnState(){
    this.setState({
      cities: this.state.storeCities,
      activeCity:[],
      activeCityName:""
    })
  }

  sortArr(a, b) {
    // Use toUpperCase() to ignore character casing
    const A = a.y
    const B = b.y
  
    let comparison = 0;
    if (A > B) {
      comparison = 1;
    } else if (A < B) {
      comparison = -1;
    }
    return comparison;
  }



  render(){
    console.log(data)
    var stateList = this.state.data.map(function(item){
      return (
          <StateButton variant="contained" color="primary" onClick={() => {this.handleState(item.state)}}>
            {capitalize(item.state)}
          </StateButton>
      )
    }, this)

    var cityList = this.state.cities.map(function(item){
      return (
        <StateButton variant="contained" color="secondary" onClick={() => {this.handleCity(item.city)}}>
          {capitalize(item.city)}
        </StateButton>
      )
    }, this)

    var activeCity = this.state.activeCity.map(function(item){
      return (
        <div style={{width:"100%"}}>
          <Typography variant="h1">
            {capitalize(item.city)}
          </Typography>
          <Grid container spacing={3}>
            <Grid item xs={12} md={8}>
              <Typography variant="h2">
                City Budget
              </Typography>
                <VictoryChart
                  style={{
                    parent: {
                      padding:'0 20px 0 150px'
                    }
                  }}
                >
                  <VictoryBar horizontal
                  
                    theme={VictoryTheme.material}
                    style={{
                      data: { fill: "#c43a31", width:5},
                      labels: {fill:'white', fontSize:6}
                    }}
                    data={item.budget.sort(this.sortArr)}
                    labels={({ datum }) => `$${(datum.y).toLocaleString()} (${(datum.y/item.total*100).toFixed(0)}%)`}
                  />
                  <VictoryAxis
                    style={{
                      axis: {stroke: "#756f6a"},
                      ticks: {stroke: "grey", size: 5, padding:5},
                      tickLabels: {fontSize: 6, padding: 5, fill:"white", overflow: "visible"}
                    }}
                  />
                </VictoryChart>
              <Typography>
                Source: {item.source}
              </Typography>
            </Grid>
            <Grid item xs={12} md={4}>
              <Typography variant="h2">
                Take Action:
              </Typography>
              {item.actions.map(function(action){
                return (
                  <div>
                    <p>{action.title}</p>
                    <p>{action.subtitle}</p>
                    <a href={`${action.link}`}>{action.action}</a>
                  </div>
                )
              })}
            </Grid>
          </Grid>
        </div>
      )
    }, this)

    return (
      <div>
        <ThemeProvider theme={theme}>
          <CssBaseline />
          <main>
            <Container maxWidth="xl">
              <Grid container spacing={0}>
                <Grid item xs={12}>
                  <Title variant="h1">
                      Police Costs
                  </Title>
                </Grid>
                <Grid item xs={12}>
                  <Crumb color="inherit" onClick={() => {this.returnHome()}} className={this.state.activeStateName.length > 0 ? "" : "hidden"}>
                    Home
                  </Crumb>
                  <Crumb color="inherit" onClick={() => {this.returnState()}} className={this.state.activeStateName.length > 0 ? "" : "hidden"}>
                    {capitalize(` / ${this.state.activeStateName}`)}
                  </Crumb>
                  <Crumb color="inherit" className={this.state.activeCityName.length > 0 ? "" : "hidden"}>
                    {capitalize(` / ${this.state.activeCityName}`)}
                  </Crumb>
                </Grid>
              </Grid>
              <Grid container spacing={3}>
                {stateList}
                {cityList}
                {activeCity}
              </Grid>
              <Box pt={4}>
                <Copyright />
              </Box>
            </Container>
          </main>
        </ThemeProvider>
      </div>

    );
  }
}

export default Dashboard;